

















import Vue, {PropOptions} from 'vue';
import {getGlbAssetUrl, getUsdzAssetUrl, isIdAnItem} from '@/common/utils/helper';
import {removeLoadingScreen} from '@/common/utils/dom';
import OpenARView from '@/configurator/components/overlays/ar/OpenARView.vue';
import {Nullable} from '@/common/utils/types';
import {EmbeddingSkin, UiInitData} from '@/configurator/embedding/types';
import {isAndroid, isIOs} from '@/common/utils/user-agent';
import {prepareSkin} from '@/common/utils/color';
// @ts-ignore
import {blackText, whiteText} from '@/common/styles/Global.module.scss';
import {RapiConfiguration, RapiItem} from '@roomle/web-sdk/lib/definitions/typings/rapi-types';

export default Vue.extend({
  components: {
    OpenARView,
  },

  props: {
    initData: Object as PropOptions<UiInitData>,
  },

  data() {
    return {
      imageUrl: null as Nullable<string>,
      gltfUrl: null as Nullable<string>,
      usdzUrl: null as Nullable<string>,
    };
  },

  async mounted() {
    if (this.initData.skin) {
      prepareSkin(this.initData, blackText, whiteText);

      for (const cssProperty in this.initData.skin) {
        if (this.initData.skin.hasOwnProperty(cssProperty)) {
          document.documentElement.style.setProperty('--' + cssProperty, this.initData.skin[cssProperty as keyof EmbeddingSkin] as string);
        }
      }
    }
    if (this.initData.id) {
      if (isIdAnItem(this.initData.id)) {
        const response = await fetch(process.env.VUE_APP_RAPI_URL + '/items/' + this.initData.id);
        const json = await response.json();
        this.setAssets(json.item);
        this.imageUrl = json.item.perspectiveImage || json.item.perspectiveImageHD;
      } else {
        const response = await fetch(process.env.VUE_APP_RAPI_URL + '/configurations/' + this.initData.id);
        const json = await response.json();
        this.setAssets(json.configuration);
        this.imageUrl = json.configuration.perspectiveImage || json.configuration.perspectiveImageHD;
      }
    }
    removeLoadingScreen();
  },

  methods: {
    setAssets(obj: RapiItem | RapiConfiguration) {
      this.gltfUrl = getGlbAssetUrl(obj);
      this.usdzUrl = getUsdzAssetUrl(obj);
    },
  },

  computed: {
    configurationId() {
      return this.initData?.id;
    },
    configuratorId() {
      return this.initData?.configuratorId;
    },
    isDesktop() {
      return !isAndroid() && !isIOs();
    },
  },
});
